.chat-content-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0.5rem 0;
}

.add-chat, .chat-input {
    max-height: 30vh;
    overflow: auto;
}

.chat-date {
    border: 2px solid black;
    margin: 0;
    padding: 0.75rem;
}

.chat-date:first-child {
    border-radius: 10px 0 0 0;
}

.chat-date:last-child {
    border-radius: 0 0 0 10px;
}

.chat-date:only-child {
    border-radius: 10px 0 0 10px;
}


.chat-text-container {
    flex-basis: 50%;
    text-overflow:ellipsis;
    overflow: hidden;
}

.chat-text {
    border: 2px solid black;
    margin: 0;
    padding: 0.75rem;
    text-overflow:ellipsis;
    overflow: hidden;
}

.chat-text:first-child {
    border-radius: 0 10px 0 0;
}

.chat-text:last-child {
    border-radius: 0 0 10px 0;
}

.chat-text:only-child {
    border-radius: 0 10px 10px 0;
}

.chat-edit-buttons, .chat-delete-buttons {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.modify-chat-button {
    height: 1.5rem;
}


.chat-info-button {
    padding: 0.25rem;
    display: block;
}