.add-person-button {
    font-size: 2rem;
    font-weight: 800;
    padding: 1rem;
    border-radius: 20px;
    margin-top: 10vh;
    margin-bottom: 5vh;
}

#add-person-div, .edit-person-container{
    display: flex;
    flex-direction: column;
    width: 90%;
    max-width: 1200px;
    row-gap: 1rem;
    border-radius: 16px;
    padding: 1.5rem;
    background: rgba(30, 33, 41, 0.48);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgb(0 0 0 / 10%);
    backdrop-filter: blur(10.3px);
    -webkit-backdrop-filter: blur(10.3px);
    margin-top: 2rem;
}
