.person-container {
    display: grid;
    grid-template-columns: 1fr minmax(50px, 1fr) minmax(50px, 1fr) 1fr;
    border-radius: 5px;
    padding: 1.5rem;
    font-size: 14px;
    width: 90%;
    max-width: 1200px;
    margin: 1rem;
    background: rgba(30, 33, 41, 0.48);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(10.3px);
    -webkit-backdrop-filter: blur(10.3px);
}

.person-container span {
    font-weight: 800;
}

.person-name {
    grid-column:  2/4;
}

.modify-person-container {
    display: flex;
    justify-content: flex-end;
    column-gap: 0.5rem;
    grid-column: 4/5;
}

.person-ocupation {
    grid-column: 1/ span 1;
}

.person-location {
    grid-column: 2/ span 1;
}

.person-met {
    grid-column: 3/ span 1;
}

.person-company {
    grid-column: 4/ span 1;
}

.add-chat {
    grid-column: 1/5;
}

.add-chat-button {
    grid-column: 2/4;
}

.chat-input {
    display: flex;
    flex-direction: column;
    grid-column: 2/4;
}

.edit-person-buttons {
    height: 3rem;
}

.modify-person-icon {
    height: 3vh;
}




